<template>
	<div class="bgtx">
		<div class="pan" v-for="item in pan" :style="{'background-image':'url('+ item.imgurl + ')'}">
			<p>{{item.a}}</p>
			<p>{{item.b}}</p>
			<p>{{item.c}}</p>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				pan:[
					{
						a:'上面',
						b:'Mid',
						c:'下面',
						imgurl: require ('../../assets/images/0.jpg')	
					},
					{
						a:'上面',
						b:'Mid',
						c:'下面',	
						imgurl: require ('../../assets/images/1.jpg')	
					},
					{
						a:'上面',
						b:'Mid',
						c:'下面',
						imgurl: require ('../../assets/images/2.jpg')	
					},
					{
						a:'上面',
						b:'Mid',
						c:'下面',
						imgurl: require ('../../assets/images/3.jpg')	
					},
					{
						a:'上面',
						b:'Mid',
						c:'下面',
						imgurl: require ('../../assets/images/4.jpg')		
					}
				]
			}
		},
		mounted(){
			const pans = document.querySelectorAll('.pan');

			function Updatepan() {
				this.classList.toggle('active')
			}

			function Updatep(event){
				if (event.propertyName.includes('flex')) {
					this.classList.toggle('activep')
				}
			}

			pans.forEach(pan => pan.addEventListener('click',Updatepan))

			pans.forEach(pan => pan.addEventListener('transitionend',Updatep))
		}
	}
</script>

<style lang="scss" scoped>
	.bgtx{
		width: 100%;
		height:calc(100vh - 3rem);
		display: flex;
		.pan{
			display: flex;
			flex: 1;
			height: 100%;
			justify-content: center;
			align-items: center;
			background-size:cover;
			background-position:center;
			flex-direction: column;
			transition: .5s cubic-bezier(0.61,-0.19, 0.7,-0.11);
			overflow: hidden;
			&.active{
				flex: 5;
			}
			&.activep > *:first-child{
				transform: translateY(0);
			}
			&.activep > *:last-child{
				transform: translateY(0);
			}
			p{
				font-size: .4rem;
				font-weight: 700;
				color:white;
				display: flex;
				flex: 1 0 auto;
				align-items:center;
				transition: .5s cubic-bezier(0.61,-0.19, 0.7,-0.11);
				&:nth-child(2){
					font-size: .6rem;
				}
				&:first-child{
					transform: translateY(-100%);
				}
				
				&:last-child{
					transform: translateY(100%);
				}
			}
		}

	}
</style>